.list_item {
    list-style: none;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
    width: 240px;
    height: 244px;
}

@media (max-width: 414px) {
    .list_item {
        height: 308px;
    }
}


.list_item:hover {
    transition: all ease 0.5s;
    box-shadow: 0px 9px 7px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
}

.link {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    line-height: 0;
    text-decoration: none;
}

.listItem_info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 12px 16px;
    color: rgba(0, 0, 0, 0.87);
    width: 100%;
}

.avatar {
    object-fit: cover;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    width: 100%;
    height: 168px;
}

@media (max-width: 414px) {
    .avatar{
        height: 232px;
    }
}


.listItem_title {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.15px;
    color: rgba(0, 0, 0, 0.87);
    margin: 0;
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}

.listItem_desc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: rgba(0, 0, 0, 0.6);
    letter-spacing: 0.25px;
    margin: 0;
}

@media (max-width: 414px) {
    .list_item {
        width: 100%;
        height: 308px;
    }
}

@media (max-width: 768px) {
    .container {
        width: 100%;
    }
}